import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import LandingPageLayout from '../../components/landing-page/landing-page-layout/landing-page-layout'
import MailchimpEmailCapture from '../../components/mailchimp-email-capture/mailchimp-email-capture'
import LandingPageHeader from '../../components/layout/landing-page-header/landing-page-header'
import Container from '@material-ui/core/Container'
import Box from '@material-ui/core/Box'
import Card from '@material-ui/core/Card'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import Hidden from '@material-ui/core/Hidden'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import Typography from '@material-ui/core/Typography'
import CardContent from '@material-ui/core/CardContent'
import Grid from '@material-ui/core/Grid'
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos'
import Img from 'gatsby-image'
import schadenmelderHero from '../../images/landing-page/schadensmelder-hero.png'
import schadenmelderBackground from '../../images/landing-page/schadensmelder-background.png'

const SchadensmelderPage = () => {
  const { mockApp1 } = useStaticQuery(graphql`
    query {
      mockApp1: file(
        relativePath: { eq: "landing-page/mock-app-001-schadensmelder.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 800) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <LandingPageLayout
      heroImage={schadenmelderHero}
      backgroundImage={schadenmelderBackground}
      usp="Machen Sie es Ihren Bürger:innen leicht, Daten über Ihre Gemeinde zu sammeln."
      sub="Saubere Daten, saubere Stadt."
    >
      <Container style={{ paddingTop: '4rem' }}>
        <LandingPageHeader label="FEATURES:" />
        <Box m={2}>
          <List aria-label="Features">
            <ListItem>
              <Hidden smDown>
                <ListItemIcon>
                  <ArrowForwardIosIcon
                    style={{ position: 'relative', top: -8, left: 0 }}
                  />
                </ListItemIcon>
              </Hidden>
              <ListItemText
                primary={
                  <Typography variant="h3" gutterBottom>
                    Die Meldedaten liegen in einer analysierbaren Struktur vor.
                    Legen Sie den Grundstein zur Smart City!
                  </Typography>
                }
              />
            </ListItem>
            <ListItem>
              <Hidden smDown>
                <ListItemIcon>
                  <ArrowForwardIosIcon
                    style={{ position: 'relative', top: -8, left: 0 }}
                  />
                </ListItemIcon>
              </Hidden>
              <ListItemText
                primary={
                  <Typography variant="h3" gutterBottom>
                    Einfacher Prozess - für Ihre Bürger:innen und für Sie.
                  </Typography>
                }
              />
            </ListItem>
            <ListItem>
              <Hidden smDown>
                <ListItemIcon>
                  <ArrowForwardIosIcon
                    style={{ position: 'relative', top: -8, left: 0 }}
                  />
                </ListItemIcon>
              </Hidden>
              <ListItemText
                primary={
                  <Typography variant="h3" gutterBottom>
                    Mobil optimiert und bedienerfreundlich gestaltet.
                  </Typography>
                }
              />
            </ListItem>
          </List>
        </Box>

        <LandingPageHeader label="IHRE VORTEILE:" />
        <Box m={2}>
          <Grid container spacing={6}>
            <Grid item xs={12}>
              <Card variant="outlined" style={{ height: '100%' }}>
                <Img
                  fluid={mockApp1.childImageSharp.fluid}
                  style={{
                    height: 0,
                    paddingTop: '56.25%',
                  }}
                />
                <CardContent>
                  <Typography variant="h6">
                    Schadensmelder sind ein Trend, der sich abzeichnet. Sie
                    werden schon von vielen Städten und Gemeinden eingesetzt.
                    Der Schadensmelder ist ein gutes digitales Einstiegsprojekt
                    - niederschwellig und verständlich für alle Beteiligten.
                    Technisch ist die Lösung leichtgewichtig und auch optisch
                    einfach auf Sie anpassbar.
                  </Typography>
                </CardContent>
              </Card>
            </Grid>
          </Grid>
        </Box>

        <MailchimpEmailCapture label="Interessiert?" />
      </Container>
    </LandingPageLayout>
  )
}

export default SchadensmelderPage
